
.vue-strongify {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 16px auto 8px;
}
.vue-strongify .bar {
  position: relative;
  height: 6px;
  width: 100%;
  margin: auto 0;
  background: #ddd;
}
.vue-strongify .bar .fill {
  background: transparent;
  height: inherit;
  position: absolute;
  width: 0;
  border-radius: inherit;
  -webkit-transition: width 0.5s ease-in-out, background 0.25s;
  transition: width 0.5s ease-in-out, background 0.25s;
}
.vue-strongify .bar .fill[data-score="1"] {
  background: #CF1919;
  width: 25%;
}
.vue-strongify .bar .fill[data-score="2"] {
  background: #E73434;
  width: 50%;
}
.vue-strongify .bar .fill[data-score="3"] {
  background: #FDB135;
  width: 75%;
}
.vue-strongify .bar .fill[data-score="4"] {
  background: #6AC072;
  width: 100%;
}
.vue-strongify .bar:before,
.vue-strongify .bar:after {
  content: "";
  height: inherit;
  background: transparent;
  display: block;
  border-color: #fff;
  border-style: solid;
  border-width: 0 5px;
  position: absolute;
  width: 50%;
  z-index: 10;
}
.vue-strongify .bar:before {
  left: 25%;
}
.vue-strongify .bar:after {
  left: 50%;
  border-right: none;
}
.vue-strongify .bar--with-comments {
  width: 70%;
}
.vue-strongify .comment {
  margin-left: auto;
  color: #929292;
  font-size: 14px;
  line-height: 6px;
}
